import { Link } from "react-router-dom";
import Parallax from "../components/Parallax";
import Button from '@mui/material/Button';

const SummarySection = () => {
    return (
        <section className="studio-section">
            <div className="section-wrapper">
                <div className="parallax-wrapper">
                    <Parallax section="summary">
                        <div className="parallax-content">
                            <div className="heading">
                                Radiosummary
                            </div>
                            <div className="description">
                                Jest rok 1991 kilkanacie miesicy po upadku komunizmu, Gdask. Reemigrant Jacek Waldemar wraca do Polski z chci dziaania. Ujrzawszy ojczysty baagan, bied i bezrobocie, uruchamia pierwsze na wybrzeu prywatne radio. To wstrzs dla Somiskiego, dyrektora lokalnej rozgoni pastwowego nadawcy. Do tej pory to, czy jego audycji ktokolwiek sucha, nie miao dla niego znaczenia. Waldemar walczy zacicie, bo wygrana suchalnoci to konkretne wpywy z reklam, ale pewnego dnia mieszkacy Gdaska maja jeszcze wikszy wybr.
                            </div>
                            <div className="button-wrapper">
                                <div className="link">
                                    <Button
                                        variant="contained"
                                        size="large"
                                        type="submit"
                                    >
                                        Zobacz zwiastun
                                    </Button>
                                </div>
                                <div className="link2">
                                    <Button
                                        variant="contained"
                                        size="large"
                                        component={Link}
                                        to={`/photo`}
                                    >
                                        Zobacz zdjcia
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </Parallax>
                </div>
            </div>
        </section>
    );
}

export default SummarySection;
